import {observable, computed, action} from 'mobx'
import * as ApiCaller from 'Utils/ApiCaller'

class UserStore {
    @observable token = null;

    @action set_token(token) {
        this.token = token;
    }

    @action async get_data(){
        console.log('start get_data')
        let a = await ApiCaller.get('https://pokeapi.co/api/v2/evolution-chain/');
        let b = await ApiCaller.get('https://pokeapi.co/api/v2/evolution-chain/');
        console.log('a')
        console.log(a)
        console.log('b')
        console.log(b)
        console.log('end get_data')
        return [a,b];
    }

    @computed get get_token() {
        return this.token;
    }
}
let store =  new UserStore();

export default store;